* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-size: 24px;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.btn {
  width: 150px;
  height: 50px;
  cursor: pointer;
  color: #fff;
  text-shadow: 1px 2px 3px #212121;
  border: rgba(0, 0, 0, 0);
  border-radius: 10px;
  font-size: 30px;
  font-weight: 900;
  box-shadow: 1px 2px 3px #212121;
}

.btn-start {
  background: #087908;
}

.btn-start:hover, .btn-start:focus {
  background: #11be11;
}

.btn-stop {
  background: red;
  margin-left: 30px;
}

.btn-stop:hover, .btn-stop:focus {
  background: #d30303;
}

.btn-start:disabled {
  color: rgba(255, 255, 255, .5);
  background: rgba(17, 190, 17, .52);
  box-shadow: 1px 2px 3px rgba(33, 33, 33, .518);
}

.btn-stop:disabled {
  color: rgba(255, 255, 255, .5);
  background: rgba(211, 3, 3, .5);
  box-shadow: 1px 2px 3px rgba(33, 33, 33, .486);
}

